// export default Quiz;
import axios from "axios";
import React, { useState, useEffect } from "react";
import Form from "react-bootstrap/Form";
import Draggable from "react-draggable";
import { API_URL } from "../App";
import { useParams } from "react-router-dom";

function Quiz() {
  const {testbankcourseid,numofques}=useParams()
  const [questions, setQuestions] = useState([]);
  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [quizFinished, setQuizFinished] = useState(false);
  const [scoreData, setScoreData] = useState(null);
  const user_id=localStorage.getItem('id')
  console.log("user",user_id)

  useEffect(() => {
    const fetchData = async () => {
      try {
        const res=await axios.get(`${API_URL}/testbank/getTestBankByIdByNumberOfQuestions/${testbankcourseid}/${numofques}/${user_id}`)
        setQuestions(res.data);
        console.log("first quiz finished", res.data)
      } catch (error) {
        console.error(error);
      }
   

      // const apiData = [
      //   {
      //     type: "radio",
      //     question: "   SEO ",
      //     options: [
      //       "          ",
      //       "      ",
      //     ],
      //   },
      //   {
      //     type: "text",
      //     question: "   SEO ",
      //   },
      //   {
      //     type: "dragdrop",
      //     question:
      //       "  ______________           _______________       : 1____________ 2___________",
      //     blanks: [
      //       { accept: "" },
      //       { accept: " " },
      //       { accept: " " },
      //       { accept: " " },
      //     ],
      //   },
      // ];
      // setQuestions(apiData);
    };
    fetchData();
  }, [testbankcourseid,numofques]);

  const handleNext = async() => {
    // try {
    //   const res=await axios.post()
    // } catch (error) {
      
    // }
    if (currentQuestionIndex < questions.length - 1) {
      setCurrentQuestionIndex(currentQuestionIndex + 1);
    } else {
      finishQuiz();
    }
  };

  const handlePrev = () => {
    if (currentQuestionIndex > 0) {
      setCurrentQuestionIndex(currentQuestionIndex - 1);
    }
  };

  const finishQuiz = () => {
    const mockScoreData = {
      user_id: 17,
      totalQuestions: 6,
      correctAnswers: 3,
      incorrectAnswers: 3,
      successRate: 50,
      failRate: 50,
      feedback: "Good effort! Focus more on weak areas.",
      incorrectQuestions: [
        {
          question_id: 11,
          user_answer: "  ",
          correct_answer: "    ",
        },
        {
          question_id: 13,
          user_answer: "  ",
          correct_answer: "",
        },
        {
          question_id: 13,
          user_answer: "  ",
          correct_answer: "",
        },
      ],
    };
    setScoreData(mockScoreData);
    setQuizFinished(true);
  };

  return (
    <div className="container courses_margin">
      {!quizFinished ? (
        questions.length > 0 && (
          <div>
            <h5 style={{ color: "#018abe", marginBottom: "20px" }}>
              {questions[currentQuestionIndex].question_text}
            </h5>

            {questions[currentQuestionIndex].question_type === "" &&
              questions[currentQuestionIndex].Answers.map((option, index) => (
                <div className="d-flex mb-3" key={index}>
                  <Form.Check type="radio" name="quizOption" className="ms-2" />
                  <span>{option.answer_text}</span>
                </div>
              ))}

            {questions[currentQuestionIndex].type === "text" && (
              <textarea rows={5} className="w-100"></textarea>
            )}

            {questions[currentQuestionIndex].type === " " && (
              <div
                className="answers"
                style={{
                  display: "flex",
                  flexWrap: "wrap",
                  justifyContent: "center",
                }}
              >
                {questions[currentQuestionIndex].blanks.map((blank, index) => (
                  <Draggable key={index}>
                    <button
                      style={{
                        backgroundColor: "#018abe",
                        border: "none",
                        borderRadius: "20px",
                        padding: "5px",
                        color: "#fff",
                        marginLeft: "5px",
                      }}
                    >
                      {blank.accept}
                    </button>
                  </Draggable>
                ))}
              </div>
            )}

            <div className="d-flex justify-content-between mt-4">
              <button
                onClick={handlePrev}
                disabled={currentQuestionIndex === 0}
                className="btn show_video_btn"
                style={{
                  backgroundColor: "#f8c36e",
                }}
              >
                
              </button>
              <button onClick={handleNext} className="btn show_video_btn">
                {currentQuestionIndex === questions.length - 1
                  ? " "
                  : ""}
              </button>
            </div>
          </div>
        )
      ) : (
        <div
          className="score-container text-center p-4" >
          <h2>  ! </h2>
          <div className="score_result">
            <h1 className="text_score">{scoreData.successRate}%</h1>
          </div>
          <div className="d-flex">
          <div className="correct_wrong_answer">
            <img src={require('../assets/checked.png')} alt="checked" title="checked" />
            <hr />
            <p>  : {scoreData.correctAnswers} /{" "}
            {scoreData.totalQuestions}</p>
          </div>
          <div className="correct_wrong_answer">
            <img src={require('../assets/no.png')} alt="checked" title="checked" />
            <hr />
            <p>  : {scoreData.incorrectAnswers}</p>
          </div>
          </div>

          {/* <h3>
              : {scoreData.correctAnswers} /{" "}
            {scoreData.totalQuestions}
          </h3>
          <h3>  : {scoreData.incorrectAnswers}</h3> */}
          {/* <h4>  : {scoreData.successRate}%</h4> */}
          <h5> {scoreData.feedback}</h5>
          <h4 style={{ marginTop: "20px", color: "red" }}>
              :
          </h4>
          <ul>
            {scoreData.incorrectQuestions.map((q, index) => (
              <li key={index}>
                 <strong>:</strong> {q.user_answer} | {" "}
                <strong>:</strong> {q.correct_answer}
                
              </li>
              
            ))}
          </ul>
  
        </div>
      )}
    </div>
  );
}

export default Quiz;
